@import './resets';
@import './typography';
@import './variables';

.container {
  max-width: 450px;
  margin: 0 auto;
  border-right: 1px solid black;
  border-left: 1px solid $grey;

  video {
    width: 100%;
    height: 236px;
  } 
  
  article {
    margin: $marginV 5%;
    text-align: center;
    // margin: 0 5%;
  }

  .link-wrap {
    display: flex;
    justify-content: space-between;
    
    a {
      margin: 35px 0 $marginV 0;
      text-decoration: none;
      display: block;
      cursor: pointer;
    }
  }

  #twin-pics {
    border: 1px red solid;
    display: flex;
  }

  .pic1 {
    background-image: url(../media/bamboo.jpg);
    width: 225px;
    height: 282px;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .pic2 {
    background-image: url(../media/karateko.jpg);
    width: 225px;
    height: 282px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

}

